.signupLabel {
  padding-left: 0.8rem;
  composes: label from "../LoginForm/LoginForm.module.scss";
}

.field {
  composes: field from "../LoginForm/LoginForm.module.scss";
}

// field placeholder-shown applied from loginForm.module.scss
.validation {
  composes: validation from "../LoginForm/LoginForm.module.scss";
}
.checkbox {
  color: #6c757d !important;
  > :first-child:focus + :last-child::before {
    box-shadow: none;
  }
}

.button {
  width: 75%;
}

// responsive

@media screen and(max-width: 767px) {
  .btn {
    width: 100%;
    display: block;
    margin: 0 auto;
  }
  .button {
    width: 100%;
  }
}
